/**
 * Placeholder Color
 */
@mixin placeholder-color( $color ) {
    &::placeholder {
        color: $color;
    }

    &::-moz-placeholder {
        color: $color;
        opacity: 1;
    }

    &::-ms-input-placeholder {
        color: $color;
    }

    &::-webkit-input-placeholder {
        color: $color;
    }

    &:focus::-webkit-input-placeholder,
    &:focus::-moz-placeholder,
    &:focus:-ms-input-placeholder,
    &:focus::placeholder {
        color: transparent;
    }
}